.sign-up-page {
  /* background-color: rgba(36, 39, 43, 1); */
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  background-size: cover;
  background-repeat: none;
}

.sign-up-overlay {
  background-color: rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.sign-up-container {
  background-color: rgba(155, 196, 203, 0.9);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  margin-top: calc(50vh - 8% - 88px);
  padding: 16px;
  width: 80%;
  justify-content: center;
  align-items: center;
}
.sign-up-form {
  display: flex;
  flex-direction: column;
  width: 75%;
  padding-top: 8px;
}

.sign-up-forms-items {
  padding: 8px;
}
